const initialState = {
	posts : [
		{id: 1, message: "How are you?", likeCount: 15},
		{id: 2, message: "Хаваю, хаваю!", likeCount: 15},
		{id: 3, message: "It's my first post", likeCount: 55},
	] as Array<PostType>,
	profile: null as ProfileType | null,
	status: "",
};

export type InitialStateType = typeof initialState;

const profileReducer = (state=initialState, action:ActionTypes):InitialStateType => {
	switch (action.type) {
		case ADD_POST: {
			const newPost = {
				id: 5,
				message: action.newPostText,
				likesCount: 0,
			};

			return {
				...state,
				posts : [newPost, ...state.posts] as Array<PostType>,
			};
		}

		case DELETE_POST: {
			return {
				...state,
				posts: state.posts.filter(p => p.id !== action.postId)
			}
		}

		case SET_USER_PROFILE:
			return {...state, profile: action.profile}

		case SET_STATUS:
			return {...state, profile: {...state.profile, status: action.status} as ProfileType}

		case SET_PHOTO:
			return {...state, profile: {...state.profile, photos: action.photos} as ProfileType}

		default: {
			return state;
		}
	}
};


type ActionTypes = AddPostCreatorActionType
	| DeletePostActionType
	| SetUsersProfileActionType
	| SetStatusActionType
	| SavePhotoSuccessActionType;

export type GetStateType = () => AppStateType;
export type DispatchType = Dispatch<ActionTypes>;

type ThunkType = ThunkAction<Promise<void>, AppStateType, unknown, ActionTypes>;

type AddPostCreatorActionType = {
	type: typeof ADD_POST,
	newPostText:string
};
export const addPostCreator = (newPostText: string):AddPostCreatorActionType => ({ type: ADD_POST, newPostText });

type DeletePostActionType = {
	type: typeof DELETE_POST,
	postId:number
};
export const deletePost = (postId: number):DeletePostActionType => ({ type: DELETE_POST, postId });

export type SetUsersProfileActionType = {
	type: typeof SET_USER_PROFILE,
	profile:ProfileType
};
export const setUsersProfile = (profile:ProfileType):SetUsersProfileActionType => ({ type: SET_USER_PROFILE, profile });

type SetStatusActionType = {
	type: typeof SET_STATUS,
	status:string,
};
export const setStatus = (status:string):SetStatusActionType => ({
	type: SET_STATUS,
	status,
});

type SavePhotoSuccessActionType = {
	type: typeof SET_PHOTO,
	photos:PhotosType
};
export const savePhotoSuccess = (photos:PhotosType):SavePhotoSuccessActionType => ({
	type: SET_PHOTO,
	photos,
});

export const getProfile = (userId:number):ThunkType => async (dispatch) => {
	const response = await profileAPI.getProfile(userId);

	dispatch(setUsersProfile(response.data));
};

export const getStatus = (status:string):ThunkType => async (dispatch) => {
	const response = await profileAPI.getStatus(status);

	dispatch(setStatus(response.data));
};

export const updateStatus = (status:string):ThunkType => async (dispatch) => {
	try {
		const response = await profileAPI.updateStatus(status);

		if (response.data.resultCode === 0) {
			dispatch(setStatus(status));
		}
	} catch (e) {
		throw e
	}
};

export const savePhoto = (file:any):ThunkType => async (dispatch) => {
	const response = await profileAPI.savePhoto(file);

	if (response.data.resultCode === 0) {
		dispatch(savePhotoSuccess(response.data.data.photos));
	}
};

export const saveProfile = (profile:ProfileType):ThunkType => async (dispatch, getState) => {
	const userId:any = getState().auth.userId;
	const response:any = await profileAPI.saveProfile(profile);

	if (response.data.resultCode === 0) {
		dispatch(setUsersProfile(userId));
	} else {
		// stopSubmit - для обработки ошибок
		const message:string = response.data.messages.length > 0 ? response.data.messages[0] : "какая-то ошибка";
		const action:any = stopSubmit("edit-profile", {_error: message});

		dispatch(action);
		return Promise.reject(message);
	}
};

export default profileReducer;