import axios from "axios";
import {ProfileType} from "../types/types";

const BASE_URL = "https://social-network.samuraijs.com/api/1.0";
const API_KEY = "bbd368cb-cbd4-49df-b254-fdd056ac38e7";

const instance = axios.create({
	withCredentials: true,
	baseURL: BASE_URL,
	headers: {
		"API-KEY": API_KEY
	}
});

export const usersAPI = {
	getUsers (currentPage:number=1, pageSize:number=0) {
		return instance.get(`/users?page=${currentPage}&count=${pageSize}`)
			.then(response => response.data);
	},
	follow(userId:number){
		return instance.post(`/follow/${userId}`);
	},
	unfollow(userId:number){
		return instance.delete(`/follow/${userId}`);
	},
};

export const profileAPI = {
	getProfile(userId:number=2){
		return instance.get(`/profile/${userId}`)
	},
	getStatus(userId:number){
		return instance.get(`profile/status/${userId}`)
	},
	updateStatus(status:string){
		return instance.put(`profile/status`, {status});
	},
	savePhoto(photoFile:any){
		const formData = new FormData();
		formData.append("image", photoFile);

		return instance.put(`profile/photo`, formData, {
			headers: {'Content-Type': 'multipart/form-data'}
		});
	},

	saveProfile(profile:ProfileType){
		return instance.put(`profile`, profile);
	},
};

export enum ResultCodesEnum {
	Success = 0,
	Error = 1,
	captchaErrorOccured = 10,
};

type MeResponseType = {
	data: {
		id:number,
		email:string,
		login:string
	},
	resultCode:ResultCodesEnum,
	messages:Array<string>
};

type LoginResponseType = {
	data: {
		userId:number,
	},
	resultCode:ResultCodesEnum,
	messages:Array<string>
};

type LogoutResponseType = {
	data: {},
	resultCode:ResultCodesEnum,
	messages:Array<string>
};

export const authAPI = {
	me(){
		return instance.get<MeResponseType>(`/auth/me`).then(response => response.data);
	},
	login(email:string, password:string, rememberMe=false, captcha:null|string=null){
		const body = {
			email,
			password,
			rememberMe,
			captcha,
		};
		return instance.post<LoginResponseType>(`/auth/login`, body).then(response => response.data);
	},
	logout(){
		return instance.delete<LogoutResponseType>(`/auth/login`).then(response => response.data);
	}
};

type CaptchaResponseType = {
	url:string
};
export const securityAPI = {
	getCaptchaUrl(){
		return instance.get<CaptchaResponseType>(`/security/get-captcha-url`).then(response => response.data);
	},
};
