import {usersAPI} from "../api/api";
import {updateObjectInArray} from "../utils/object-helper";
import {UsersType} from "../types/types";
import {Dispatch} from "redux";
import {AppStateType} from "./redux-store";
import {ThunkAction, ThunkDispatch} from "redux-thunk";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const SET_TOTAL_USERS_COUNT = "SET_TOTAL_USERS_COUNT";
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING";
const TOGGLE_IS_FOLLOWING_PROGRESS = "TOGGLE_IS_FOLLOWING_PROGRESS";


const initialState = {
	users : [] as Array<UsersType>,
	pageSize: 50,
	totalUsersCount: 0,
	currentPage: 1,
	isFetching: false,
	followingInProgress: [] as Array<number>, // array of users ids
};

type InitialStateType = typeof initialState;
const usersReducer = (state=initialState, action:ActionsTypes):InitialStateType => {
	switch (action.type) {

		case FOLLOW:
			return {
				...state,
				// @ (items, itemId, objPropName, newObjProps)
				users: updateObjectInArray(state.users, action.userId, "id", {followed: true})
			};

		case UNFOLLOW:
			return {
				...state,
				users: updateObjectInArray(state.users, action.userId, "id", {followed: false})
			};

		case SET_USERS:
			return {...state, users: [...action.users]};

		case SET_CURRENT_PAGE:
			return {...state, currentPage: action.currentPage};

		case SET_TOTAL_USERS_COUNT:
			return {...state, totalUsersCount: action.count};

		case TOGGLE_IS_FETCHING:
			return {...state, isFetching: action.isFetching};

		case TOGGLE_IS_FOLLOWING_PROGRESS:
			return {
				...state,
				followingInProgress: action.isFetching ?
					[...state.followingInProgress, action.userId]
						:
					state.followingInProgress.filter(id => id != action.userId),
			};

		default: {
			return state;
		}

	}

};


type ActionsTypes = FollowSuccessActionType |
	UnfollowSuccessActionType |
	SetUsersActionType |
	SetCurrentPageActionType |
	SetTotalUsersActionType |
	ToggleIsFetchingActionType |
	TogglefollowingProgressActionType;

export type GetStateType = () => AppStateType;
export type DispatchType = Dispatch<ActionsTypes>;

type FollowSuccessActionType = {
	type: typeof FOLLOW,
	userId: number,
};
export const followSuccess = (userId:number):FollowSuccessActionType => ({ type: FOLLOW, userId});

type UnfollowSuccessActionType = {
	type: typeof UNFOLLOW,
	userId: number,
};
export const unfollowSuccess = (userId:number):UnfollowSuccessActionType => ({ type: UNFOLLOW, userId });

type SetUsersActionType = {
	type: typeof SET_USERS,
	users: Array<UsersType>,
};
export const setUsers = (users:Array<UsersType>):SetUsersActionType => ({ type: SET_USERS, users });

type SetCurrentPageActionType = {
	type: typeof SET_CURRENT_PAGE,
	currentPage: number,
};
export const setCurrentPage = (currentPage:number):SetCurrentPageActionType => ({ type: SET_CURRENT_PAGE, currentPage  });

type SetTotalUsersActionType = {
	type: typeof SET_TOTAL_USERS_COUNT,
	count: number,
};
export const setTotalUsersCount = (totalUsersCount:number):SetTotalUsersActionType => ({ type: SET_TOTAL_USERS_COUNT, count: totalUsersCount  });

type ToggleIsFetchingActionType = {
	type: typeof TOGGLE_IS_FETCHING,
	isFetching: boolean,
};
export const toggleIsFetching = (isFetching:boolean):ToggleIsFetchingActionType => ({ type: TOGGLE_IS_FETCHING, isFetching  });

type TogglefollowingProgressActionType = {
	type: typeof TOGGLE_IS_FOLLOWING_PROGRESS,
	isFetching: boolean,
	userId:number,
};
export const togglefollowingProgress = (isFetching:boolean, userId:number):TogglefollowingProgressActionType => ({
	type: TOGGLE_IS_FOLLOWING_PROGRESS,
	isFetching,
	userId,
});



type ThunkType = ThunkAction<Promise<void>, AppStateType, unknown, ActionsTypes>;

export const getUsersThunkCreator = (currentPage:number, pageSize:number):ThunkType => {
	return async (dispatch) => {
		dispatch(toggleIsFetching(true));
		dispatch(setCurrentPage(currentPage));

		const data = await usersAPI.getUsers(currentPage, pageSize);

		dispatch(setUsers(data.items));
		dispatch(setTotalUsersCount(data.totalCount));
		dispatch(toggleIsFetching(false));
	}
};

const _followUnfollowToggle = async ( dispatch:DispatchType,
																		 userId:number,
																		 apiMethod:any,
																		 actionCreator: (userID:number) => FollowSuccessActionType | UnfollowSuccessActionType ) => {
	dispatch(togglefollowingProgress(true, userId));

	const response = await apiMethod(userId);

	if (response.data.resultCode === 0) {
		dispatch(actionCreator(userId));
	}
	dispatch(togglefollowingProgress(false, userId));
};

export const follow = (userId:number):ThunkType => {
	return async (dispatch) => {
		const apiMethod = await usersAPI.follow.bind(usersAPI);

		_followUnfollowToggle(dispatch, userId, apiMethod, followSuccess);
	}
};

export const unfollow = (userId:number):ThunkType => {
	return async (dispatch) => {
		const apiMethod = await usersAPI.unfollow.bind(usersAPI);

		_followUnfollowToggle(dispatch, userId, apiMethod, unfollowSuccess);
	}
};

export default usersReducer;