import {authAPI, ResultCodesEnum, securityAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {ThunkAction} from "redux-thunk";
import {AppStateType} from "./redux-store";

const SET_USER_DATA = "samurai/auth/SET_USER_DATA";
const GET_CAPTCHA_URL_SUCCESS = "samurai/auth/GET_CAPTCHA_URL_SUCCESS";

// export type InitialStateType = {
// 	userId: number | null,
// 	email: string | null,
// 	login: string | null,
// 	isAuth: boolean,
// 	captchaUrl: string | null,
// };
const initialState = {
	userId: null as number | null,
	email: null as string | null,
	login: null as string | null,
	isAuth: false,
	captchaUrl: null as string | null, // if null, then captcha is not required
};
export type InitialStateType = typeof initialState;

const authReducer = (state=initialState, action:ActionTypes): InitialStateType => {

	switch (action.type){
		case SET_USER_DATA:
		case GET_CAPTCHA_URL_SUCCESS : {
			return {
				...state,
				...action.payload
			};
		}

		default:
			return state;
	}

};

type ActionTypes = SetAuthUserDataActionType | GetCaptchaUrlSuccessActionType;
type ThunkType = ThunkAction<Promise<void>, AppStateType, unknown, ActionTypes>;

type SetAuthUserDataActionPayloadType = {
	userId:number | null,
	email:string | null,
	login:string | null,
	isAuth:boolean,
};
type SetAuthUserDataActionType = {
	type: typeof SET_USER_DATA,
	payload:SetAuthUserDataActionPayloadType
};
export const setAuthUserData = (userId:number | null, email:string | null, login:string | null, isAuth:boolean): SetAuthUserDataActionType => ({
	type: SET_USER_DATA,
	payload: {
		userId,
		email,
		login,
		isAuth,
	}
});


type GetCaptchaUrlSuccessActionType = {
	type: typeof GET_CAPTCHA_URL_SUCCESS,
	payload: { captchaUrl: string },
};
export const getCaptchaUrlSuccess = (captchaUrl: string):GetCaptchaUrlSuccessActionType => ({
	type: GET_CAPTCHA_URL_SUCCESS,
	payload: {captchaUrl},
});


export const getAuthUserData = ():ThunkType =>  async (dispatch) => {
	const meData = await authAPI.me();

	if (meData.resultCode === ResultCodesEnum.Success){
		const {id, email, login} = meData.data;
		dispatch(setAuthUserData(id, email, login, true));
	}
};

export const login = (email:string,
											password:string,
											rememberMe:boolean,
											captcha:string):ThunkType => async (dispatch) => {
	const data = await authAPI.login(email, password, rememberMe, captcha);

	if (data.resultCode === ResultCodesEnum.Success){
		dispatch(getAuthUserData());

	} else {

		if (data.resultCode === ResultCodesEnum.captchaErrorOccured) {
			dispatch(getCaptchaUrl());
		}

		// stopSubmit - для обработки ошибок
		const message:string = data.messages.length > 0 ? data.messages[0] : "какая-то ошибка";
		const action:any = stopSubmit("login", {_error: message});

		dispatch(action);
	}
};

export const getCaptchaUrl = ():ThunkType => async (dispatch) => {
	const data = await securityAPI.getCaptchaUrl();

	dispatch( getCaptchaUrlSuccess(data.url) );
};

export const logout = ():ThunkType => async (dispatch ) => {
	const data = await authAPI.logout();

	if (data.resultCode === ResultCodesEnum.Success){
		dispatch(setAuthUserData(null, null, null, true));
	}
};


export default authReducer;