const ADD_MESSAGE = "ADD-MESSAGE";

type DialogsType = {
	id: number,
	name: string,
};
type MessagesType = {
	id: number,
	message: string,
};

const initialState = {
	dialogs: [
		{id: 1, name: "Dima",},
		{id: 2, name: "Lesha",},
		{id: 3, name: "Masha",},
		{id: 4, name: "Lena",},
		{id: 5, name: "Pasha",},
		{id: 6, name: "Dasha",},
	] as Array<DialogsType>,
	messages: [
		{id: 1, message: "How are you?",},
		{id: 2, message: "Yuo mmen",},
		{id: 3, message: "Hi",},
	] as Array<MessagesType>,
};
export type InitialStateType = typeof initialState;

const dialogsReducer = (state=initialState, action:ActionTypes):InitialStateType => {
	switch (action.type){
		case ADD_MESSAGE: {
			return {
				...state,
				messages : [...state.messages, {id: 5, message: action.newMessageBody}]
			};
		}

		default:
			return state;
	}
};

type ActionTypes = AddMessageCreatorActionType;

type AddMessageCreatorActionType = {
	type: typeof ADD_MESSAGE,
	newMessageBody: string
};
export const addMessageCreator = (newMessageBody:string):AddMessageCreatorActionType => ({
	type: ADD_MESSAGE,
	newMessageBody
});

export default dialogsReducer;